@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@600&display=swap');

.about {
  padding: 1rem 3rem 3rem;
  color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  background-color: rgba(255, 255, 255, 0.9); /* Fallback color */
}

.about-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  width: 100%;
  gap: 2rem;
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0.75); /* Overlay box effect */
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.about-left {
  flex: 1.2;
  min-width: 300px;
  text-align: left;
  padding-right: 2rem;
}

.about-title {
  font-style:serif;
  font-size: 2.7rem;
  font-weight: bold;
  margin-bottom: 1rem;
  text-transform: uppercase;
  color: #0f0d0d; 
  box-shadow: #201b1b;
  
}



.about-title:hover
{
   transform: scale(1.02);
  color: #000000;
}

.about-description {
  font-size: 1.3rem;
  line-height: 1.9;
  margin-bottom: 1rem;
  font-style: oblique;
  font-weight: bolder;
  color: #201b1b;
  text-shadow: #ffffff;
}
.about-description:hover{
  transform: scale(1.02);
  color: #26177c;
}
.about-right {
  flex: 0.8;
  min-width: 280px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.about-image {
  width: 280px;
  height: 280px;
  border-radius: 50%;
  border: 5px solid #ffffff;
  background-color: rgb(22, 104, 140);
  object-fit: contain; /* Shows full image */
  object-position: center;
  margin-top: 0;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}


.about-image:hover {
  transform: scale(1.03);
}

.about-name {
  font-family: 'Playfair Display', serif;
  font-size: 2rem;
  margin-top: 1rem;
  color: #2c3e50;
  font-weight: 600;
  letter-spacing: 1px;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, color 0.3s ease;
}

.about-name:hover {
  transform: scale(1.05);
  color: #000000;
}
